<template>
  <div class="add-motion-record-box2" >
    <SimpleHeader :name="'运动追踪'"  />
    <van-cell title="运动日期" is-link :value="dateValue" @click="()=> datePickShow = true" />
    <van-cell title="运动类型"  is-link :value="typeValue" @click="()=> momtionPickShow = true" />
    <van-cell title="运动时长" is-link :value="`${timeValue.hh}:${timeValue.mm}`" @click="()=> timePickShow = true"  />
  </div>

  <van-button @click="onSave" class="van-button--primary" type="primary" loading-text="加载中..." block>
    保存
  </van-button>

  <!--  日期选择 -->
  <van-popup
      v-model:show="datePickShow"
      position="bottom"
  >
    <van-datetime-picker
        item-height="25"
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        :formatter="formatter"
    />
  </van-popup>

<!--  时长选择 -->
  <van-popup
      v-model:show="timePickShow"
      position="bottom"
  >
    <van-picker
        item-height="25"
        visible-item-count="5"
        :columns="columns"
        @cancel="()=> timePickShow = false"
        @confirm="onTimeConfirm" />
  </van-popup>
<!--  运动类型选择-->
  <van-popup
      v-model:show="momtionPickShow"
      position="bottom"
  >
    <van-picker
        item-height="25"
        visible-item-count="5"
        :columns="montionColumns"
        @cancel="()=> momtionPickShow = false"
        @confirm="onMontionConfirm" />
  </van-popup>

</template>

<script>
import SimpleHeader from "@/components/SimpleHeader";
import {onMounted, reactive, toRefs } from "vue";
import {useRoute, useRouter} from "vue-router";
import moment from 'moment'
import { Toast } from 'vant';
import { getSportTypeList, addSportRecord } from '@/service/motion'
import {formatYYMMDD, formatYYMMDDHHMMSS} from "@/utils/util";

export default {
name: "add-record",
  components: {
    SimpleHeader
  },
  setup(props){

    const route = useRoute()
    const router = useRouter()

    onMounted(()=>{
       const { query: { selectdate } } = route
       console.log(selectdate)
       state.currentDate = new Date(selectdate)
       state.maxDate = new Date(selectdate)
       // 时间初始化
       state.dateValue = moment(new Date(selectdate)).format("YYYY-MM-DD")
       for (let i = 0; i < 24; i++) {
         state.columns[0].values.push(`${ i < 10 ? "0"+i : i }时`)
       }
       for (let i = 0; i < 60; i++) {
         state.columns[1].values.push(`${ i < 10 ? "0"+i : i }分`)
       }
      requstMomtionTypeList()
    })



    const formatter = (type, val) => {
      if (type === 'year') {
        return `${val}年`;
      }
      if (type === 'month') {
        return `${val}月`;
      }
      if (type === 'day') {
        return `${val}日`;
      }
      return val;
    };
    // 运动选择
    const onMontionConfirm = (value,index) =>{
      state.momtionPickShow = false
      console.log("typeValue",value)
      state.typeValue = value[0].text + "," + value[1].text
      state.sportType = value[1].value
      state.heat = value[1].heat
    }
    // 确认时间选择
    const onTimeConfirm = (value,index) => {
    console.log('确认时间选择',value)
      state.timePickShow = false
      state.timeValue.hh = value[0].substr(0,value[0].length-1)
      state.timeValue.mm = value[1].substr(0,value[1].length-1)
    }

  const state = reactive({
    heat: 0,
    sportType: 0,
    typeValue: "请选择",
    //时间选择器显示标识
    datePickShow: false,
    currentDate: new Date(),
    minDate: new Date(2000, 0, 1),
    maxDate: new Date(2025, 10, 1),
    // 时间选择
    dateValue:  moment(new Date()).format("YYYY-MM-DD"),
    timeValue: { hh: '00',mm: '00'},
    //运动时长弹层显示标识
    timePickShow: false,
    //运动时长所列选择
    columns:[
      // 第一列
      {
        values: [],
        defaultIndex: 2,
      },
      // 第二列
      {
        values: [],
        defaultIndex: 1,
      },
    ],
    momtionPickShow: false,
    montionColumns: []
  })

  /**
   * 获取运动类型联动列表
   * @returns {Promise<void>}
   */
  const requstMomtionTypeList = async () => {
    const res = await getSportTypeList()
    const { code, data = []} = res
    let newList = []
    newList = JSON.parse(JSON.stringify(data).replace(/label/g, 'text'))
    console.log("获取运动类型联动列表",newList)
    state.montionColumns = newList
  }

  // 保存
  const onSave = async () => {
    if(Number(state.timeValue.hh) === 0 && Number(state.timeValue.mm) === 0){
      Toast("运动时长不能为空")
      return
    }else if(state.typeValue === "请选择"){
      Toast("运动类型不能为空")
      return
    }
    const typss = state.typeValue.split(",")
    const sportTime = Number(state.timeValue.hh) * 60 +  Number(state.timeValue.mm)
    const res = await addSportRecord(sportTime, state.sportType, formatYYMMDD( state.dateValue), typss[1], state.heat)
    const { code, data } = res
    if(code === 200){
      router.push("/motion")
    }else{
      Toast("添加数据异常")
    }

  }
  return {
    ...toRefs(state),
    onSave,
    onTimeConfirm,
    formatter,
    onMontionConfirm
  }
}


}
</script>

<style  lang="less">

.add-motion-record-box2{
  .van-cell__value{
    color: #333;
  }
}

.van-button--primary {
  position: absolute;
  bottom: 0px;
  color: #fff;
  background-color: #06B48D;
  border: 1px solid #06B48D;
}

</style>
