import axios, {BASEURL} from '../../utils/axios'
import {getUserId} from "@/utils/util";

/**
 * 当日运动界面详情
 * @returns {*}
 */
export function getDayDetial(sportDateDesc,day) {
  return axios.get(`/monitor/health/sport/syLogSportHistory/getSportHistoryDetail`,{
    params:{
      sportDateDesc, day, userId: getUserId()
    }
  });
}

// 数据选择类型
export function getSportTypeList() {
  return axios.get(`/monitor/health/sport/sportType/getList`);
}

// 添加运动数据
export function addSportRecord(sportTime,sportType,sportDateDesc, name, heat) {
  const data = []
  const params = { sportTime, sportType, sportDateDesc, name, heat  }
  data.push(params)
  return axios.post(`/monitor/health/sport/syLogSportHistory/insert`,data);
}

// 添加运动数据
export function getSyLogSportHistoryByTime(startTime,endTime) {
  return axios(`/monitor/health/sport/syLogSportHistory/getSyLogSportHistoryByTime`,{
    params: { startTime, endTime,userId: getUserId()}
  });
}
